export default {
    layout: "Dum-content",
       mounted() {
        this.getContacts();
  },
    data() {
        return {
            contacts: [],
           
        };
    },
   
    methods: {
        getContacts() {
      this.$axios
        .$get("/contact/show")
        .then((res) => {
          this.contacts = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },
}


}
